* {
    box-sizing: border-box;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
        Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #fafafa;
    color: #212121;
    line-height: 1.5;
}

#search-box {
    font: inherit;
    padding: 8px;
    max-width: 320px;
    margin-bottom: 16px;
}

.card-img,
.card-title {
    display: inline-block;
}

.card-title {
    font-size: 36px;
    margin-left: 10px;
}

.card-text {
    font-size: 22px;
}

.card-text span {
    font-weight: 700;
}

.card-item {
    list-style: none;
}

p.card-title {
    font-size: 22px;
}
